.sendMessageAreaFromCurrentDialog {
    align-self: end;
    display: grid;
    grid-template-columns: 1fr;
    padding-top: 10px;
    height: 80px;
    min-height: 60px;
    outline-color: #492F7E;
    resize: none;


}


.textArea {
    border-radius: 10px 10px 0px 0px ;
    border: 2px solid #492F7E;
    outline-color: #492F7E;
    resize: none;
}



.sendMessageAreaFromCurrentDialog button {
    width: 100%;
    height: 30px;
    background-color: #492F7E;
    color: white;
    border: none;
    transition-duration: 0.4s;
    outline: none;
}

.sendMessageAreaFromCurrentDialog button:hover {
    background-color: #69BA85;
    color: #050000;
}

.sendMessageAreaFromCurrentDialog button:active {
    box-shadow: inset 0px 1px 10px 2px #492F7E;
    color: white;
}

